.footer {
  display: flex;
  flex-direction: column;
  padding-top: 25px;
  padding-bottom: 25px;
  background: maroon;
}

.footer__nav {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
  color: white;
}

.footer__links {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__links > a {
  color: white;
}

.footer__social {
  display: flex;
  justify-content: center;
}
.footer__socialItem {
  padding-left: 20px;
  padding-right: 20px;
  color: white;
}
.footer__socialItem:hover {
  color: #e8ae0c;
  transform: scale(2);
}

.footer__bottom {
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

.footer__bottom p {
  color: white;
}
